.page {
  box-sizing: border-box;
  margin: 0;
  font-family: Roboto, sans-serif;
}

.card {
  align-items: center;
  width: 200px;
  display: flex;
}

.card__content {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  padding-top: 32px;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__information {
  color: #060b35;
  flex-direction: column;
  margin: 0 16px;
  display: flex;
}

.card__title {
  margin-top: 0;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-top: 0;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__rating {
  color: #060b35;
  justify-content: space-between;
  align-items: end;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__reviews {
  margin: 0;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  margin-bottom: 16px;
  display: flex;
}

.card__price--name {
  color: #616070;
  text-align: left;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
}

.card__price--amount {
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin-bottom: 16px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 700;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:nth-child(-n+4) {
  margin-right: 4px;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

